.wp-block-talkwave-tags {
	.tags-list {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 16px;
		padding: 0;
		margin: 0;
		list-style: none;
	}

	.tag-link {
		display: flex;
		align-items: flex-end;
		min-height: 100px;
		padding: 10px 15px;
		border-radius: 8px;
		font-weight: bold;
		text-decoration: none;
		transition: background-color 0.3s ease, color 0.3s ease;
		background-color: var(--wp--preset--color--primary);
		color: var(--wp--preset--color--foreground);
	}

	.tag-link:hover {
		background-color: var(--wp--preset--color--tertiary);
		color: var(--wp--preset--color--background);
	}

	& li:nth-child(1) .tag-link {
		background-color: #4158D0;
		background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
	}

	& li:nth-child(2) .tag-link {
		background-color: #0093E9;
		background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);

	}

	& li:nth-child(3) .tag-link {
		background-color: #0093E9;
		background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);

	}

	& li:nth-child(4) .tag-link {
		background-color: #D9AFD9;
		background-image: linear-gradient(0deg, #D9AFD9 0%, #97D9E1 100%);

	}

	& li:nth-child(5) .tag-link {
		background-color: #00DBDE;
		background-image: linear-gradient(90deg, #00DBDE 0%, #FC00FF 100%);

	}

	& li:nth-child(6) .tag-link {
		background-color: #8BC6EC;
		background-image: linear-gradient(135deg, #8BC6EC 0%, #9599E2 100%);

	}

	& li:nth-child(7) .tag-link {
		background-color: #08AEEA;
		background-image: linear-gradient(0deg, #08AEEA 0%, #2AF598 100%);

	}

	& li:nth-child(8) .tag-link {
		background-color: #FA8BFF;
		background-image: linear-gradient(45deg, #FA8BFF 0%, #2BD2FF 52%, #2BFF88 90%);

	}
}